<script>
  import ChatListMobile from './chat/ChatListMobile.svelte';
  import ChatContentMobile from './chat/ChatContentMobile.svelte';

  let screen = 'list'; // list | detail
  let activeChat = null;

  const openChat = (chat) => {
    activeChat = chat;
    screen = 'detail';
  };

  const back = () => {
    screen = 'list';
  };
</script>

{#if screen === 'list'}
  <ChatListMobile {openChat} />
{:else if screen === 'detail'}
  <ChatContentMobile {activeChat} {back} />
{/if}
