// src/lib/websocket-client.js
import { writable, get } from 'svelte/store';
import { user, accessToken } from '$lib/stores';
import ApiClient from '$lib/api';
const api = new ApiClient();

export const status = writable(' Connecting...');
export const users = writable([]);
export const messages = writable([]);
export const hasMore = writable(true);


export const myId = writable(null);
export const myName = writable('');

export const conversations = writable([]);
export const conversationId = writable(null);
export const oldestMessageId = writable(null);

//  Listen to user store changes
user.subscribe(u => {
    if (u) {
        myId.set(u.id);
        myName.set(u.name);
    } else {
        myId.set(null);
        myName.set('');
    }
});

const WS_URL = import.meta.env.VITE_WS_URL;

let socket = null;
let reconnectTimer = null;
let manualClose = false; //  quan trng
/* ========================================================
    CONNECT WEBSOCKET  WITH COOKIE (refreshToken)
   Backend s c cookie refreshToken  login WS
======================================================== */
export function connectWebSocket() {
    console.log('connectWebSocket....', socket?.readyState, WebSocket.CLOSED)
    if (socket && socket.readyState !== WebSocket.CLOSED) {
        console.log(" WS already connected or connecting");
        return socket;
    }

    const token = get(accessToken);
    // console.log('ws connect token:', token)
    if (!token) {
        console.log("No access token  DO NOT CONNECT WS yet.");
        return;
    }
    manualClose = false; //  reset khi connect mi
    socket = new WebSocket(WS_URL);

    socket.onopen = () => {
        socket.send(JSON.stringify({
            type: "auth",
            token: token
        }));
        status.set(" Connected");
    };


    /* ========================================================
        RECEIVE MESSAGE
    ======================================================== */
    socket.onmessage = async (event) => {
        try {
            const data = JSON.parse(event.data);

            //console.log('data msg:', data)

            if (data.type === 'auth_error') {

                console.warn('Token expired  refreshing...', new Date().toISOString());

                try {
                    const res = await api.post(
                        '/auth/refresh',
                        {},
                        { withCredentials: true }
                    );

                    const newToken = res.accessToken;
                    accessToken.set(newToken);

                    sendWhenOpen({
                        type: 'auth',
                        token: newToken
                    });

                } catch (err) {
                    console.error('Li Refresh token failed:', err);
                    socket.close();
                }
                return;
            }

            if (data.type === "recent_contacts") {
                //console.log('recent_contacts:', data.contacts)
                conversations.set([...data.conversations]);
                return;
            }

            // Welcome message
            if (data.type === "welcome") {
                console.log('welcome', data)
                //messages.update(m => [...m, data]);
                return;
            }

            // Chat message
            if (data.type === "chat") {
                const msg = data?.payload ?? {};
                if (!isMessageForCurrentChat(msg)) return;
                messages.update(m => [...m, msg]);
                return;
            }

            // if (data.type === "messages") {
            //     const { rows } = data;
            //     rows.reverse();
            //     console.log(rows)
            //     messages.set(rows);
            //     return;
            // }

            if (data.type === "messages") {
                if (data.rows.length === 0) return;

                //console.log('first111: ', get(messages)[0])

                messages.update(m => [...data.rows, ...m]);
                return;
            }

            if (data.type === "reaction_update") {
                const { messageId, reactions } = data;

                messages.update(list =>
                    list.map(m =>
                        m.id === messageId
                            ? { ...m, reactions }
                            : m
                    )
                );
                return;
            }


        } catch (err) {
            console.warn(" Invalid WS message:", event.data);
        }
    };

    /* ========================================================
        AUTO RECONNECT
    ======================================================== */
    socket.onclose = () => {
        const token = get(accessToken);

        if (manualClose || !token) {
            console.log(" WS closed manually or token missing  NO reconnect");
            status.set(" WS closed");
            return;
        }
        console.log(" WS disconnected  retry in 2s...");
        status.set(" Disconnected  retrying...");

        clearTimeout(reconnectTimer);
        reconnectTimer = setTimeout(connectWebSocket, 2000);
    };

    function sendWhenOpen(payload) {
        //console.log('sendWhenOpen:', socket.readyState === WebSocket.OPEN, payload)

        if (!socket) return;

        if (socket.readyState === WebSocket.OPEN) {
            socket.send(JSON.stringify(payload));
        } else {
            const onOpen = () => {
                socket.send(JSON.stringify(payload));
                socket.removeEventListener('open', onOpen);
            };
            socket.addEventListener('open', onOpen);
        }
    }

    return socket;
}

/* ========================================================
    SEND MESSAGE (NO accessToken)
======================================================== */
function isMessageForCurrentChat(msg) {
    const currentConversationId = get(conversationId);

    if (!currentConversationId) return false;

    return msg.conversationId === currentConversationId;
}

export function sendMessage(data) {
    if (!socket || socket.readyState !== WebSocket.OPEN) {
        console.warn(" WS not ready");
        return;
    }
    //console.log('send:', data)
    socket.send(JSON.stringify(data));
}

/* ========================================================
    BINARY SUPPORT
======================================================== */
export const sendChunk = (buffer) =>
    socket?.readyState === WebSocket.OPEN && socket.send(buffer);

export const sendStop = () =>
    socket?.readyState === WebSocket.OPEN && socket.send("stop");

/* ========================================================
    CLOSE MANUALLY
======================================================== */
export function closeSocket() {
    if (socket) {
        console.log(" Closing WS manually...");
        manualClose = true; //  nh du close ch 
        clearTimeout(reconnectTimer);
        socket.close();
        socket = null;
    }
}

export function loadMessagesWS(conversationId, opts = {}) {
    if (!socket || socket.readyState !== WebSocket.OPEN) return;

    socket.send(JSON.stringify({
        type: "load_messages",
        conversationId,
        firstMsg: opts.firstMsg ?? null,
    }));
}

/* ========================================================
    LOAD MESSAGE HISTORY (Backend)
======================================================== */
export async function loadMessages(before = null) {
    try {
        const u = get(user);
        if (!u) return;

        let url = `${import.meta.env.VITE_API_BASE_URL}/messages?userId=${u.id}`;
        if (before) url += `&before=${before}`;

        const res = await fetch(url, {
            headers: {
                // Backend /messages cn accessToken
                Authorization: `Bearer ${localStorage.getItem("accessToken")}`
            },
            credentials: "include"
        });

        const rows = await res.json();

        if (rows.length < 50) hasMore.set(false);

        messages.update(m => [...rows, ...m]);

    } catch (err) {
        console.error(" loadMessages error:", err);
    }
}
