// src/lib/api.js
import { get } from "svelte/store";
import { accessToken, user } from "$lib/stores";
import { getRefreshToken, saveRefreshToken, clearRefreshToken } from "$lib/db/indexeddb";

/**
 * ApiClient
 * - auto refresh access token when receive 401
 * - queue requests while refresh in progress
 * - retry original request with new token (only once)
 */
export default class ApiClient {
    constructor({
        baseURL = "",
        withCredentials = false,
        defaultHeaders = { "Content-Type": "application/json" },
        defaultResponseType = "json"
    } = {}) {
        this.baseURL =
            import.meta.env.VITE_API_BASE_URL ||
            baseURL ||
            `${location.protocol}//${location.host}`;

        this.withCredentials = withCredentials;
        this.defaultHeaders = defaultHeaders;
        this.defaultResponseType = defaultResponseType;

        this._isRefreshing = false;
        this._refreshQueue = []; // [{resolve, reject}]
        this.latestAccessToken = null; // in-memory fast token
    }

    // ---------------------------
    // Token helpers
    // ---------------------------
    setAccessToken(token) {
        if (typeof token === "string") {
            accessToken.set(token);
            this.latestAccessToken = token;
        } else {
            accessToken.set("");
            this.latestAccessToken = null;
        }
    }

    // ---------------------------
    // Queue helpers
    // ---------------------------
    _enqueueRefresh(resolve, reject) {
        this._refreshQueue.push({ resolve, reject });
    }

    _resolveRefreshQueue(token) {
        this._refreshQueue.forEach(q => q.resolve(token));
        this._refreshQueue = [];
    }

    _rejectRefreshQueue(err) {
        this._refreshQueue.forEach(q => q.reject(err));
        this._refreshQueue = [];
    }

    // ---------------------------
    // Refresh access token (singleton)
    // ---------------------------
    async _refreshAccessToken() {
        if (this._isRefreshing) {
            // if already refreshing, return a promise that resolves when refresh finishes
            return new Promise((resolve, reject) => {
                this._enqueueRefresh(resolve, reject);
            });
        }

        this._isRefreshing = true;

        try {
            const storedRefresh = await getRefreshToken();
            if (!storedRefresh) throw new Error("Refresh token not found");

            const res = await fetch(`${this.baseURL}/auth/refresh`, {
                method: "POST",
                headers: { "Content-Type": "application/json" },
                body: JSON.stringify({ refreshToken: storedRefresh })
            });

            if (!res.ok) {
                // treat as refresh failure
                throw new Error(`Refresh failed: HTTP ${res.status}`);
            }

            const data = await res.json();
            if (!data?.accessToken) {
                throw new Error("Refresh response missing accessToken");
            }

            // update token in-memory + svelte store
            this.setAccessToken(data.accessToken);

            // optionally update refresh token in storage
            if (data.refreshToken) {
                await saveRefreshToken(data.refreshToken);
            }

            // resolve waiting callers with new token
            this._resolveRefreshQueue(data.accessToken);

            return data.accessToken;
        } catch (err) {
            // refresh failed -> clear auth state
            try {
                await clearRefreshToken();
            } catch (e) {
                console.warn("clearRefreshToken failed:", e);
            }

            this.setAccessToken(""); // clear store + memory
            try {
                user.set(null);
            } catch (e) {
                // if store not available, ignore
            }

            // reject waiting callers
            this._rejectRefreshQueue(err);

            throw err;
        } finally {
            this._isRefreshing = false;
        }
    }

    // ---------------------------
    // Core request with automatic refresh + retry
    // ---------------------------
    async request(endpoint, {
        method = "GET",
        data = null,
        params = null,
        headers = {},
        responseType = this.defaultResponseType,
        withCredentials = this.withCredentials,
        retry = true // allow one retry after refresh
    } = {}) {
        const url = `${this.baseURL}${endpoint}${params ? "?" + new URLSearchParams(params) : ""}`;

        // prefer in-memory token (fresh), fallback to svelte store
        const token = this.latestAccessToken ?? get(accessToken) ?? "";

        const composedHeaders = {
            ...(data instanceof FormData ? {} : this.defaultHeaders),
            ...(token ? { Authorization: `Bearer ${token}` } : {}),
            ...headers
        };

        const options = {
            method,
            credentials: withCredentials ? "include" : "same-origin",
            headers: composedHeaders
        };

        if (data) {
            options.body = data instanceof FormData ? data : JSON.stringify(data);
        }

        let res;
        try {
            res = await fetch(url, options);
        } catch (networkErr) {
            // network error
            throw networkErr;
        }

        // If unauthorized and retry allowed -> attempt refresh
        if (res.status === 401 && retry) {
            try {
                // If a refresh is already in progress, _refreshAccessToken() will wait till it's done
                const newToken = await this._refreshAccessToken();

                // Retry the request once with new token  ensure Authorization header uses new token
                const retryHeaders = {
                    ...(data instanceof FormData ? {} : this.defaultHeaders),
                    ...(headers || {}),
                    ...(newToken ? { Authorization: `Bearer ${newToken}` } : {})
                };

                const retryOptions = {
                    method,
                    credentials: withCredentials ? "include" : "same-origin",
                    headers: retryHeaders
                };

                if (data) {
                    retryOptions.body = data instanceof FormData ? data : JSON.stringify(data);
                }

                const retryRes = await fetch(url, retryOptions);

                if (!retryRes.ok) {
                    // final failure after refresh
                    throw new Error(`HTTP ${retryRes.status}: ${retryRes.statusText}`);
                }

                // parse body
                return this._parseResponseByType(retryRes, responseType);
            } catch (err) {
                // refresh or retry failed -> propagate
                throw err;
            }
        }

        if (!res.ok) {
            throw new Error(`HTTP ${res.status}: ${res.statusText}`);
        }

        return this._parseResponseByType(res, responseType);
    }

    _parseResponseByType(res, responseType) {
        switch (responseType) {
            case "json": return res.json();
            case "text": return res.text();
            case "blob": return res.blob();
            case "arrayBuffer": return res.arrayBuffer();
            case "formData": return res.formData();
            default: return res;
        }
    }

    // ---------------------------
    // Convenience methods
    // ---------------------------
    get(endpoint, opts = {}) { return this.request(endpoint, { ...opts, method: "GET" }); }
    post(endpoint, data, opts = {}) { return this.request(endpoint, { ...opts, method: "POST", data }); }
    put(endpoint, data, opts = {}) { return this.request(endpoint, { ...opts, method: "PUT", data }); }
    delete(endpoint, opts = {}) { return this.request(endpoint, { ...opts, method: "DELETE" }); }
}

// default instance
export const api = new ApiClient();

// helper functions
export async function postImage(blob) {
    const formData = new FormData();
    formData.append("image", blob, "capture.jpg");
    // note: content-type will not be set manually because it's FormData
    return api.post("/capture", formData);
}

export async function streamInput(chunk, isLast = false) {
    const formData = new FormData();
    formData.append("chunk", chunk, isLast ? "final.webm" : "chunk.webm");
    formData.append("stop", isLast.toString());
    return api.post("/stream-input", formData);
}
