// src/lib/stores/persist.js
import { writable } from 'svelte/store';

export function persist(key, defaultValue) {
    const isObject = typeof defaultValue === 'object' && defaultValue !== null;

    let initialValue = defaultValue;

    if (typeof window !== 'undefined') {
        try {
            const saved = localStorage.getItem(key);
            if (saved !== null) {
                if (isObject) {
                    initialValue = { ...defaultValue, ...JSON.parse(saved) };
                } else {
                    initialValue = saved;
                }
            }
        } catch { }
    }

    const store = writable(initialValue);

    store.subscribe((value) => {
        try {
            localStorage.setItem(
                key,
                isObject ? JSON.stringify(value) : value
            );
        } catch { }
    });

    return store;
}

